.cheq-form-title {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  color: black;
  margin-bottom: 5px;
}

.cheq-form-title .paste-all .error-tooltip::after {
  border-top-color: var(--secondary-red);
}

.cheq-form-title > :last-child {
  margin: 0 10px;
}

.paste-all {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
}

.paste-all :first-child {
  margin: 0 5px;
}

.paste-all:hover {
  cursor: pointer;
}

.paste-all .cc-error.tooltip {
  position: absolute;
  white-space: nowrap;
  top: -65px;
  color: var(--red);
  background-color: var(--secondary-red);
  border-radius: 6px;
  padding: 10px 8px;
}

.paste-all .cc-error.tooltip::after {
  position: absolute;
  content: "";
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid var(--secondary-red);
  bottom: -5px;
  left: 50%;
}

.button-essential button {
  border-radius: 4px;
  height: 52px;
  margin-top: 8px;
}

.margin-left-16 {
  margin-left: 16px;
}
.cheq-subtitle {
  font-size: 12px;
  color: var(--bw-black, #282421);
}

.cheq-subtitle b {
  font-family: sans-serif;
  color: black;
}
.button-remove-essential button {
  background: white;
  border: 1px solid var(--brand-essential-pink);
  color: var(--brand-essential-pink);
}
