.info-box-background {
  background: #e6f7ff;
  /* Indication/Mid Blue */

  border: 1px solid #91d5ff;
  /* Card */

  box-shadow: 0px 0px 30px rgba(102, 102, 102, 0.05);
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 17px;
}
.info-box-background span {
  color: rgba(0, 80, 179, 1);
  font-weight: 300 !important;
  margin-left: 5px;
  font-size: 14px;
}
